.order_number {
  composes: text text_type_digits-large mt-9 mb-8 from global;
}

.subtitle {
  composes: text text_type_main-medium from global;
}

.text {
  composes: text text_type_main-default mb-2 from global;
}

.text_secondary {
  color: var(--text-inactive-color);
  composes: pb-30 from global;
}

.icon {
  composes: mt-15 mb-15 from global;
}

.close {
  align-self: flex-end;
  cursor: pointer;
}

.name {
  width: 80%;
  composes: text text_type_main-default mb-2 mt-2 from global;
  color: var(--text-inactive-color);
}