.picture {
  max-width: 240px;
  max-height: 120px;
  composes: ml-4 mr-4 from global;
}

.image {
  width: 100%;
}

.item {
  display: flex;
  flex-direction: column;
  max-width: 272px;
  position: relative;
  composes: ml-6 mb-10 from global;
  cursor: pointer;
  color: var(--text-primary-color);
  text-decoration: none;
}

.price {
  display: flex;
  justify-content: center;
  align-items: center;
  composes: mt-1 mb-1 text_type_digits-medium from global;
}

.text {
  composes: text text_type_main-default from global;
  min-height: 48px;
  text-align: center;
}
.card:focus {
  box-shadow: 1px 1px 0.5px 1px;
}