.wrapper {
    background-image: url('https://pictures.s3.yandex.net/resources/Frame_5_1618586219.png');
    background-position: 36% 73%;
    background-repeat: no-repeat;
    height: 75vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.text {
    composes: text text_type_main-medium mt-4 from global;
}

.title {
    composes: text text_type_digits-large from global;
}

.link {
    composes: text text_type_main-medium from global;
    text-decoration: none;
    color: var(--colors-interface-accent)
}