.dashboard {
    composes: ml-15 pr-2 pt-25 from global;
    box-sizing: border-box;
    width: 580px;
}

.content {
    composes: mb-15 from global;
    display: flex;
}

.content:last-child {
    margin-bottom: 0px;
}

.content_item {
    composes: pr-9 from global;
    width: 50%;
}

.content_item:last-child {
    padding-right: 0px;
}

.list {
    list-style: none;
    padding: 0;
    composes: text text_type_digits-default mt-6 from global;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    flex-grow: 0;
    max-height: 20vh;
    overflow: hidden;
}

.title {
    composes: pb-6 from global;
}

.row {
    display: flex;
    flex-direction: column;
    composes: mb-15 from global;
}

.row__title {
    composes: text text_type_main-medium from global;
}

.row__content {
    composes: text text_type_digits-large from global;
}

.ready:first-of-type, .inprogress:first-of-type {
    margin: 0;
}

.ready {
    color: var(--colors-interface-success);
}

.inprogress {
    composes: mt-2 from global;
}